import React, {Component} from 'react';
import './BuildListPage.css';
import { AppContext } from '../../stores/Store';
import BuildsList from './BuildsList';

class BuildListPage extends Component {
    static contextType = AppContext;

  render() {
    const [state, updateState] = this.context;

    document.title = "Latest Builds - Vermintide 2 Builds - Vermintide 2 | Ranalds.Gift";
    //alert(state.difficulty ? state.difficulty.id : 0);

    return (
        <div className="build-list-page">
          <BuildsList careerId={state.careerId} user={state.user} difficulty={state.difficulty} mission={state.mission} potion={state.potion} book={state.book}></BuildsList>
        </div>
    );
  }
}

export default BuildListPage;